.desktop-container {
    width: 100%;
    height: 100%;
}

.nav-desktop {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(34, 34, 34);
    width: 100%;
    height: 10vh; /* Escalable según la altura de la pantalla */
    box-sizing: border-box;
    padding: 0 2vw; /* Añadir padding en relación al ancho de la pantalla */
}

.logo-desktop {
    width: 4vw; /* Ajuste relativo al ancho de la pantalla */
    height: auto;
    margin-top: 1vh;
    padding: 0 1vw;
}

.desktop-name {
    margin: 0;
    padding: 0;
    font-size: 2.6rem; /* Tamaño escalable de la fuente */
    color: rgb(145, 28, 28);
    letter-spacing: 0.5vw; /* Espaciado de letras relativo al ancho */
    cursor: default;
    padding-left: 22vw;
}

.desktop-menu ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    gap: 3vw; /* Espacio entre elementos de la lista */
    padding: 0 3vw;
}

.desktop-menu li {
    font-size: 1.5rem; /* Tamaño de fuente escalable */
    color: white;
    cursor: pointer;
    transition: color 0.5s ease;
}

.desktop-menu li:hover {
    color: rgb(75, 0, 0);
}

.desktop-body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 90vh; /* Escalable con la altura de la pantalla */
    overflow: hidden;
}

.left-container, .right-container {
    flex: 1;
    display: flex;
    height: 100%;
}

.logo-desktop-body {
    max-width: 20vw; /* Tamaño del logo relativo al ancho */
    height: auto;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    filter: drop-shadow(
        0px 0px 10px rgba(0, 0, 0, 0.8)
    ) 
}

.left-container {
    background-color: black;
    clip-path: polygon(0 0, 100% 0, 85% 100%, 0 100%);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.mono-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    transition: opacity 1.5s ease-in-out;
    opacity: 0;
}

.mono-image.visible {
    opacity: 1;
}

.mono-image.hidden {
    opacity: 0;
}

.desktop-filter {
    background-color: rgba(182, 100, 100, 0.5);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

.right-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    color: white;
    margin-bottom: 8vh; /* Espacio inferior escalable */
    padding: 0 -10vw; /* Padding adaptable según el ancho de la pantalla */
}

.right-container h1 {
    font-size: 2.5rem; /* Tamaño de fuente escalable */
    margin-bottom: 1vh; /* Espacio inferior relativo a la altura */
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    letter-spacing: 0.2rem; /* Espaciado de letras escalable */
    margin-left: 0;
}

.right-container p {
    font-size: 1.2rem; /* Tamaño de fuente escalable */
    line-height: 2; /* Espaciado entre líneas */
    max-width: 20vw; /* Ancho máximo escalable */
    margin: 0 auto; /* Centrado automático */
}
